import useTRouterStore from '@/stores/tRouter';
import { useRoute, useRouter } from 'vue-router';

export default function useTRouter() {
  const router = useRouter();
  const route = useRoute();

  const tRouterStore = useTRouterStore();

  function push(...args: Parameters<typeof router.push>) {
    router.push(...args);
  }

  function back() {
    router.go(-1);
    tRouterStore.updateIsBack(true);
  }

  function goHome() {
    router.replace('/');
    tRouterStore.updateIsBack(true);
  }

  return {
    router: { ...router, push, back, goHome },
    route
  };
}
